html {
  box-sizing: border-box;
}

html *, :after, :before {
  box-sizing: inherit;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.un_use, .hid {
  display: none;
}

.no-scroll {
  overflow: hidden;
}

h1, h2, h3, h4, h5, p, ul, ol, li {
  margin: 0;
}

ul, ol {
  list-style: none;
}

img {
  display: block;
}

ul {
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  margin: 0 auto;
  font-family: Inter, sans-serif;
  line-height: 14px;
}

button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  margin: 0;
  padding: 0;
}

a {
  color: #000;
  text-decoration: none;
}

input {
  margin: 0;
  padding: 0;
}

input:focus {
  outline: none;
}

svg {
  display: block;
}

